.bar {
  text-align: center;
}

.bar > input,
.search-btn {
  font-size: 1.3rem;
}

.bar > input {
  margin-right: 1rem;
  padding: 1rem;
  border: none;
  border-radius: 5px;
  background: #ddd;
  outline: none;
}

.bar > input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:active {
  background: #ddd;
}

.search-btn {
  background: #333;
  color: #fff;
  border-radius: 50%;
  width: 3.6rem;
  transform: scale(1);
}

.search-btn:hover {
  transform: scale(1.1);
  opacity: 1;
}

@media screen and (max-width: 568px) {
  .bar > input {
    font-size: 1.1rem;
  }
}
