.gallery {
  margin: 1.5rem 0;
  column-count: 3;
}

.gallery > div {
  margin-bottom: 1rem;
  position: relative;
}

.gallery > div > img {
  height: auto;
  width: 100%;
}

.gallery > div > img:hover + div,
.gallery > div > div:hover {
  opacity: 1;
}

.user-overlay {
  opacity: 0;
  width: 100%;
  padding: 0.5rem;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  z-index: 10;
  bottom: 0.3rem;
  left: 0;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.user-overlay > a {
  padding: 0.5rem 0.8rem;
  border: none;
  border-radius: 2px;
  background: #f4f4f4;
  color: #333;
  text-decoration: none;
  transition: all 0.2s ease;
}

.user-overlay > a:hover {
  opacity: 0.9;
}

.user {
  display: flex;
  align-items: center;
}

.user img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}

.user p {
  color: #fff;
  margin-left: 1rem;
}

.error {
  text-align: center;
}

.error h1 {
  font-size: 2rem;
}

@media screen and (max-width: 768px) {
  .gallery {
    column-count: 1;
  }

  .user-overlay {
      position: relative;
      background: #fff;
      margin-top: 0.5rem;
      opacity: 1;
  }

  .user p {
      color: #333;
  }

  .user-overlay > a {
      background: #ddd;
      color: #333;
  }

}
